ight !default;\n$border-light      : $grey-lightest !default;\n$border-light-hover: $grey-light !default;\n\n// Text colors\n\n$text       : $grey-dark !default;\n$text-invert: findColorInvert($text) !default;\n$text-light : $grey !default;\n$text-strong: $grey-darker !default;\n\n// Code colors\n\n$code           : $red !default;\n$code-background: $background !default;\n\n$pre            : $text !default;\n$pre-background : $background !default;\n\n// Link colors\n\n$link              : $blue !default;\n$link-invert       : findColorInvert($link) !default;\n$link-light        : findLightColor($link) !default;\n$link-dark         : findDarkColor($link) !default;\n$link-visited      : $purple !default;\n\n$link-hover        : $grey-darker !default;\n$link-hover-border : $grey-light !default;\n\n$link-focus        : $grey-darker !default;\n$link-focus-border : $blue !default;\n\n$link-active       : $grey-darker !default;\n$link-active-border: $grey-dark !default;\n\n// Typography\n\n$family-primary  : $family-sans-serif !default;\n$family-secondary: $family-sans-serif !default;\n$family-code     : $family-monospace !default;\n\n$size-small : $size-7 !default;\n$size-normal: $size-6 !default;\n$size-medium: $size-5 !default;\n$size-large : $size-4 !default;\n\n// Lists and maps\n$custom-colors: null !default;\n$custom-shades: null !default;\n\n$colors: mergeColorMaps(\n(\n  \"white\"  : ($white, $black),\n  \"black\"  : ($black, $white),\n  \"light\"  : ($light, $light-invert),\n  \"dark\"   : ($dark, $dark-invert),\n  \"primary\": ($primary, $primary-invert, $primary-light, $primary-dark),\n  \"link\"   : ($link, $link-invert, $link-light, $link-dark),\n  \"info\"   : ($info, $info-invert, $info-light, $info-dark),\n  \"success\": ($success, $success-invert, $success-light, $success-dark),\n  \"warning\": ($warning, $warning-invert, $warning-light, $warning-dark),\n  \"danger\" : ($danger, $danger-invert, $danger-light, $danger-dark)),\n  $custom-colors\n) !default;\n\n$shades: mergeColorMaps(\n(\n  \"black-bis\"   : $black-bis,\n  \"black-ter\"   : $black-ter,\n  \"grey-darker\" : $grey-darker,\n  \"grey-dark\"   : $grey-dark,\n  \"grey\"        : $grey,\n  \"grey-light\"  : $grey-light,\n  \"grey-lighter\": $grey-lighter,\n  \"white-ter\"   : $white-ter,\n  \"white-bis\"   : $white-bis),\n  $custom-shades\n) !default;\n\n$sizes: $size-1 $size-2 $size-3 $size-4 $size-5 $size-6 $size-7 !default;\n","$tag-background-color: $background !default\n$tag-color: $text !default\n$tag-radius: $radius !default\n$tag-delete-margin: 1px !default\n\n.tags\n  align-items: center\n  display: flex\n  flex-wrap: wrap\n  justify-content: flex-start\n  .tag\n    margin-bottom: 0.5rem\n    &:not(:last-child)\n      +ltr-property(\"margin\", 0.5rem)\n  &:last-child\n    margin-bottom: -0.5rem\n  &:not(:last-child)\n    margin-bottom: 1rem\n  // Sizes\n  &.are-medium\n    .tag:not(.is-normal):not(.is-large)\n      font-size: $size-normal\n  &.are-large\n    .tag:not(.is-normal):not(.is-medium)\n      font-size: $size-medium\n  &.is-centered\n    justify-content: center\n    .tag\n      margin-right: 0.25rem\n      margin-left: 0.25rem\n  &.is-right\n    justify-content: flex-end\n    .tag\n      &:not(:first-child)\n        margin-left: 0.5rem\n      &:not(:last-child)\n        margin-right: 0\n  &.has-addons\n    .tag\n      +ltr-property(\"margin\", 0)\n      &:not(:first-child)\n        +ltr-property(\"margin\", 0, false)\n        +ltr\n          border-top-left-radius: 0\n          border-bottom-left-radius: 0\n        +rtl\n          border-top-right-radius: 0\n          border-bottom-right-radius: 0\n      &:not(:last-child)\n        +ltr\n          border-top-right-radius: 0\n          border-bottom-right-radius: 0\n        +rtl\n          border-top-left-radius: 0\n          border-bottom-left-radius: 0\n\n.tag:not(body)\n  align-items: center\n  background-color: $tag-background-color\n  border-radius: $tag-radius\n  color: $tag-color\n  display: inline-flex\n  font-size: $size-small\n  height: 2em\n  justify-content: center\n  line-height: 1.5\n  padding-left: 0.75em\n  padding-right: 0.75em\n  white-space: nowrap\n  .delete\n    +ltr-property(\"margin\", 0.25rem, false)\n    +ltr-property(\"margin\", -0.375rem)\n  // Colors\n  @each $name, $pair in $colors\n    $color: nth($pair, 1)\n    $color-invert: nth($pair, 2)\n    &.is-#{$name}\n      background-color: $color\n      color: $color-invert\n      // If a light and dark colors are provided\n      @if length($pair) > 3\n        $color-light: nth($pair, 3)\n        $color-dark: nth($pair, 4)\n        &.is-light\n          background-color: $color-light\n          color: $color-dark\n  // Sizes\n  &.is-normal\n    font-size: $size-small\n  &.is-medium\n    font-size: $size-normal\n  &.is-large\n    font-size: $size-medium\n  .icon\n    &:first-child:not(:last-child)\n      +ltr-property(\"margin\", -0.375em, false)\n      +ltr-property(\"margin\", 0.1875em)\n    &:last-child:not(:first-child)\n      +ltr-property(\"margin\", 0.1875em, false)\n      +ltr-property(\"margin\", -0.375em)\n    &:first-child:last-child\n      +ltr-property(\"margin\", -0.375em, false)\n      +ltr-property(\"margin\", -0.375em)\n  // Modifiers\n  &.is-delete\n    +ltr-property(\"margin\", $tag-delete-margin, false)\n    padding: 0\n    position: relative\n    width: 2em\n    &::before,\n    &::after\n      background-color: currentColor\n      content: \"\"\n      display: block\n      left: 50%\n      position: absolute\n      top: 50%\n      transform: translateX(-50%) translateY(-50%) rotate(45deg)\n      transform-origin: center center\n    &::before\n      height: 1px\n      width: 50%\n    &::after\n      height: 50%\n      width: 1px\n    &:hover,\n    &:focus\n      background-color: darken($tag-background-color, 5%)\n    &:active\n      background-color: darken($tag-background-color, 10%)\n  &.is-rounded\n    border-radius: $radius-rounded\n\na.tag\n  &:hover\n    text-decoration: underline\n","$title-color: $text-strong !default\n$title-family: false !default\n$title-size: $size-3 !default\n$title-weight: $weight-semibold !default\n$title-line-height: 1.125 !default\n$title-strong-color: inherit !default\n$title-strong-weight: inherit !default\n$title-sub-size: 0.75em !default\n$title-sup-size: 0.75em !default\n\n$subtitle-color: $text !default\n$subtitle-family: false !default\n$subtitle-size: $size-5 !default\n$subtitle-weight: $weight-normal !default\n$subtitle-line-height: 1.25 !default\n$subtitle-strong-color: $text-strong !default\n$subtitle-strong-weight: $weight-semibold !default\n$subtitle-negative-margin: -1.25rem !default\n\n.title,\n.subtitle\n  @extend %block\n  word-break: break-word\n  em,\n  span\n    font-weight: inherit\n  sub\n    font-size: $title-sub-size\n  sup\n    font-size: $title-sup-size\n  .tag\n    vertical-align: middle\n\n.title\n  color: $title-color\n  @if $title-family\n    font-family: $title-family\n  font-size: $title-size\n  font-weight: $title-weight\n  line-height: $title-line-height\n  strong\n    color: $title-strong-color\n    font-weight: $title-strong-weight\n  & + .highlight\n    margin-top: -0.75rem\n  &:not(.is-spaced) + .subtitle\n    margin-top: $subtitle-negative-margin\n  // Sizes\n  @each $size in $sizes\n    $i: index($sizes, $size)\n    &.is-#{$i}\n      font-size: $size\n\n.subtitle\n  color: $subtitle-color\n  @if $subtitle-family\n    font-family: $subtitle-family\n  font-size: $subtitle-size\n  font-weight: $subtitle-weight\n  line-height: $subtitle-line-height\n  strong\n    color: $subtitle-strong-color\n    font-weight: $subtitle-strong-weight\n  &:not(.is-spaced) + .title\n    margin-top: $subtitle-negative-margin\n  // Sizes\n  @each $size in $sizes\n    $i: index($sizes, $size)\n    &.is-#{$i}\n      font-size: $size\n",".block\n  @extend %block\n\n.delete\n  @extend %delete\n\n.heading\n  display: block\n  font-size: 11px\n  letter-spacing: 1px\n  margin-bottom: 5px\n  text-transform: uppercase\n\n.highlight\n  @extend %block\n  font-weight: $weight-normal\n  max-width: 100%\n  overflow: hidden\n  padding: 0\n  pre\n    overflow: auto\n    max-width: 100%\n\n.loader\n  @extend %loader\n\n.number\n  align-items: center\n  background-color: $background\n  border-radius: $radius-rounded\n  display: inline-flex\n  font-size: $size-medium\n  height: 2em\n  justify-content: center\n  margin-right: 1.5rem\n  min-width: 2.5em\n  padding: 0.25rem 0.5rem\n  text-align: center\n  vertical-align: top\n","$input-color: $text-strong !default\n$input-background-color: $scheme-main !default\n$input-border-color: $border !default\n$input-height: $control-height !default\n$input-shadow: inset 0 0.0625em 0.125em rgba($scheme-invert, 0.05) !default\n$input-placeholder-color: bulmaRgba($input-color, 0.3) !default\n\n$input-hover-color: $text-strong !default\n$input-hover-border-color: $border-hover !default\n\n$input-focus-color: $text-strong !default\n$input-focus-border-color: $link !default\n$input-focus-box-shadow-size: 0 0 0 0.125em !default\n$input-focus-box-shadow-color: bulmaRgba($link, 0.25) !default\n\n$input-disabled-color: $text-light !default\n$input-disabled-background-color: $background !default\n$input-disabled-border-color: $background !default\n$input-disabled-placeholder-color: bulmaRgba($input-disabled-color, 0.3) !default\n\n$input-arrow: $link !default\n\n$input-icon-color: $border !default\n$input-icon-active-color: $text !default\n\n$input-radius: $radius !default\n\n=input\n  @extend %control\n  background-color: $input-background-color\n  border-color: $input-border-color\n  border-radius: $input-radius\n  color: $input-color\n  +placeholder\n    color: $input-placeholder-color\n  &:hover,\n  &.is-hovered\n    border-color: $input-hover-border-color\n  &:focus,\n  &.is-focused,\n  &:active,\n  &.is-active\n    border-color: $input-focus-border-color\n    box-shadow: $input-focus-box-shadow-size $input-focus-box-shadow-color\n  &[disabled],\n  fieldset[disabled] &\n    background-color: $input-disabled-background-color\n    border-color: $input-disabled-border-color\n    box-shadow: none\n    color: $input-disabled-color\n    +placeholder\n      color: $input-disabled-placeholder-color\n\n%input\n  +input\n","$textarea-padding: $control-padding-horizontal !default\n$textarea-max-height: 40em !default\n$textarea-min-height: 8em !default\n\n%input-textarea\n  @extend %input\n  box-shadow: $input-shadow\n  max-width: 100%\n  width: 100%\n  &[readonly]\n    box-shadow: none\n  // Colors\n  @each $name, $pair in $colors\n    $color: nth($pair, 1)\n    &.is-#{$name}\n      border-color: $color\n      &:focus,\n      &.is-focused,\n      &:active,\n      &.is-active\n        box-shadow: $input-focus-box-shadow-size bulmaRgba($color, 0.25)\n  // Sizes\n  &.is-small\n    +control-small\n  &.is-medium\n    +control-medium\n  &.is-large\n    +control-large\n  // Modifiers\n  &.is-fullwidth\n    display: block\n    width: 100%\n  &.is-inline\n    display: inline\n    width: auto\n\n.input\n  @extend %input-textarea\n  &.is-rounded\n    border-radius: $radius-rounded\n    padding-left: calc(#{$control-padding-horizontal} + 0.375em)\n    padding-right: calc(#{$control-padding-horizontal} + 0.375em)\n  &.is-static\n    background-color: transparent\n    border-color: transparent\n    box-shadow: none\n    padding-left: 0\n    padding-right: 0\n\n.textarea\n  @extend %input-textarea\n  display: block\n  max-width: 100%\n  min-width: 100%\n  padding: $textarea-padding\n  resize: vertical\n  &:not([rows])\n    max-height: $textarea-max-height\n    min-height: $textarea-min-height\n  &[rows]\n    height: initial\n  // Modifiers\n  &.has-fixed-size\n    resize: none\n","%checkbox-radio\n  cursor: pointer\n  display: inline-block\n  line-height: 1.25\n  position: relative\n  input\n    cursor: pointer\n  &:hover\n    color: $input-hover-color\n  &[disabled],\n  fieldset[disabled] &\n    color: $input-disabled-color\n    cursor: not-allowed\n\n.checkbox\n  @extend %checkbox-radio\n\n.radio\n  @extend %checkbox-radio\n  & + .radio\n    +ltr-property(\"margin\", 0.5em, false)\n",".select\n  display: inline-block\n  max-width: 100%\n  position: relative\n  vertical-align: top\n  &:not(.is-multiple)\n    height: $input-height\n  &:not(.is-multiple):not(.is-loading)\n    &::after\n      @extend %arrow\n      border-color: $input-arrow\n      +ltr-position(1.125em)\n      z-index: 4\n  &.is-rounded\n    select\n      border-radius: $radius-rounded\n      +ltr-property(\"padding\", 1em, false)\n  select\n    @extend %input\n    cursor: pointer\n    display: block\n    font-size: 1em\n    max-width: 100%\n    outline: none\n    &::-ms-expand\n      display: none\n    &[disabled]:hover,\n    fieldset[disabled] &:hover\n      border-color: $input-disabled-border-color\n    &:not([multiple])\n      +ltr-property(\"padding\", 2.5em)\n    &[multiple]\n      height: auto\n      padding: 0\n      option\n        padding: 0.5em 1em\n  // States\n  &:not(.is-multiple):not(.is-loading):hover\n    &::after\n      border-color: $input-hover-color\n  // Colors\n  @each $name, $pair in $colors\n    $color: nth($pair, 1)\n    &.is-#{$name}\n      &:not(:hover)::after\n        border-color: $color\n      select\n        border-color: $color\n        &:hover,\n        &.is-hovered\n          border-color: bulmaDarken($color, 5%)\n        &:focus,\n        &.is-focused,\n        &:active,\n        &.is-active\n          box-shadow: $input-focus-box-shadow-size bulmaRgba($color, 0.25)\n  // Sizes\n  &.is-small\n    +control-small\n  &.is-medium\n    +control-medium\n  &.is-large\n    +control-large\n  // Modifiers\n  &.is-disabled\n    &::after\n      border-color: $input-disabled-color\n  &.is-fullwidth\n    width: 100%\n    select\n      width: 100%\n  &.is-loading\n    &::after\n      @extend %loader\n      margin-top: 0\n      position: absolute\n      +ltr-position(0.625em)\n      top: 0.625em\n      transform: none\n    &.is-small:after\n      font-size: $size-small\n    &.is-medium:after\n      font-size: $size-medium\n    &.is-large:after\n      font-size: $size-large\n","$file-border-color: $border !default\n$file-radius: $radius !default\n\n$file-cta-background-color: $scheme-main-ter !default\n$file-cta-color: $text !default\n$file-cta-hover-color: $text-strong !default\n$file-cta-active-color: $text-strong !default\n\n$file-name-border-color: $border !default\n$file-name-border-style: solid !default\n$file-name-border-width: 1px 1px 1px 0 !default\n$file-name-max-width: 16em !default\n\n.file\n  @extend %unselectable\n  align-items: stretch\n  display: flex\n  justify-content: flex-start\n  position: relative\n  // Colors\n  @each $name, $pair in $colors\n    $color: nth($pair, 1)\n    $color-invert: nth($pair, 2)\n    &.is-#{$name}\n      .file-cta\n        background-color: $color\n        border-color: transparent\n        color: $color-invert\n      &:hover,\n      &.is-hovered\n        .file-cta\n          background-color: bulmaDarken($color, 2.5%)\n          border-color: transparent\n          color: $color-invert\n      &:focus,\n      &.is-focused\n        .file-cta\n          border-color: transparent\n          box-shadow: 0 0 0.5em bulmaRgba($color, 0.25)\n          color: $color-invert\n      &:active,\n      &.is-active\n        .file-cta\n          background-color: bulmaDarken($color, 5%)\n          border-color: transparent\n          color: $color-invert\n  // Sizes\n  &.is-small\n    font-size: $size-small\n  &.is-medium\n    font-size: $size-medium\n    .file-icon\n      .fa\n        font-size: 21px\n  &.is-large\n    font-size: $size-large\n    .file-icon\n      .fa\n        font-size: 28px\n  // Modifiers\n  &.has-name\n    .file-cta\n      border-bottom-right-radius: 0\n      border-top-right-radius: 0\n    .file-name\n      border-bottom-left-radius: 0\n      border-top-left-radius: 0\n    &.is-empty\n      .file-cta\n        border-radius: $file-radius\n      .file-name\n        display: none\n  &.is-boxed\n    .file-label\n      flex-direction: column\n    .file-cta\n      flex-direction: column\n      height: auto\n      padding: 1em 3em\n    .file-name\n      border-width: 0 1px 1px\n    .file-icon\n      height: 1.5em\n      width: 1.5em\n      .fa\n        font-size: 21px\n    &.is-small\n      .file-icon .fa\n        font-size: 14px\n    &.is-medium\n      .file-icon .fa\n        font-size: 28px\n    &.is-large\n      .file-icon .fa\n        font-size: 35px\n    &.has-name\n      .file-cta\n        border-radius: $file-radius $file-radius 0 0\n      .file-name\n        border-radius: 0 0 $file-radius $file-radius\n        border-width: 0 1px 1px\n  &.is-centered\n    justify-content: center\n  &.is-fullwidth\n    .file-label\n      width: 100%\n    .file-name\n      flex-grow: 1\n      max-width: none\n  &.is-right\n    justify-content: flex-end\n    .file-cta\n      border-radius: 0 $file-radius $file-radius 0\n    .file-name\n      border-radius: $file-radius 0 0 $file-radius\n      border-width: 1px 0 1px 1px\n      order: -1\n\n.file-label\n  align-items: stretch\n  display: flex\n  cursor: pointer\n  justify-content: flex-start\n  overflow: hidden\n  position: relative\n  &:hover\n    .file-cta\n      background-color: bulmaDarken($file-cta-background-color, 2.5%)\n      color: $file-cta-hover-color\n    .file-name\n      border-color: bulmaDarken($file-name-border-color, 2.5%)\n  &:active\n    .file-cta\n      background-color: bulmaDarken($file-cta-background-color, 5%)\n      color: $file-cta-active-color\n    .file-name\n      border-color: bulmaDarken($file-name-border-color, 5%)\n\n.file-input\n  height: 100%\n  left: 0\n  opacity: 0\n  outline: none\n  position: absolute\n  top: 0\n  width: 100%\n\n.file-cta,\n.file-name\n  @extend %control\n  border-color: $file-border-color\n  border-radius: $file-radius\n  font-size: 1em\n  padding-left: 1em\n  padding-right: 1em\n  white-space: nowrap\n\n.file-cta\n  background-color: $file-cta-background-color\n  color: $file-cta-color\n\n.file-name\n  border-color: $file-name-border-color\n  border-style: $file-name-border-style\n  border-width: $file-name-border-width\n  display: block\n  max-width: $file-name-max-width\n  overflow: hidden\n  text-align: inherit\n  text-overflow: ellipsis\n\n.file-icon\n  align-items: center\n  display: flex\n  height: 1em\n  justify-content: center\n  +ltr-property(\"margin\", 0.5em)\n  width: 1em\n  .fa\n    font-size: 14px\n","$label-color: $text-strong !default\n$label-weight: $weight-bold !default\n\n$help-size: $size-small !default\n\n.label\n  color: $label-color\n  display: block\n  font-size: $size-normal\n  font-weight: $label-weight\n  &:not(:last-child)\n    margin-bottom: 0.5em\n  // Sizes\n  &.is-small\n    font-size: $size-small\n  &.is-medium\n    font-size: $size-medium\n  &.is-large\n    font-size: $size-large\n\n.help\n  display: block\n  font-size: $help-size\n  margin-top: 0.25rem\n  @each $name, $pair in $colors\n    $color: nth($pair, 1)\n    &.is-#{$name}\n      color: $color\n\n// Containers\n\n.field\n  &:not(:last-child)\n    margin-bottom: 0.75rem\n  // Modifiers\n  &.has-addons\n    display: flex\n    justify-content: flex-start\n    .control\n      &:not(:last-child)\n        +ltr-property(\"margin\", -1px)\n      &:not(:first-child):not(:last-child)\n        .button,\n        .input,\n        .select select\n          border-radius: 0\n      &:first-child:not(:only-child)\n        .button,\n        .input,\n        .select select\n          +ltr\n            border-bottom-right-radius: 0\n            border-top-right-radius: 0\n          +rtl\n            border-bottom-left-radius: 0\n            border-top-left-radius: 0\n      &:last-child:not(:only-child)\n        .button,\n        .input,\n        .select select\n          +ltr\n            border-bottom-left-radius: 0\n            border-top-left-radius